.nav1 {
  /* font-family: "segoe ui"; */
  font-family: "Karla", sans-serif;

  height: 58px;
  width: 100%;
  background-color: #14406d;
  /* position: relative; */
  position: fixed; /* Set the navbar to fixed position */
  top: 0;
  z-index: 100;
}

.nav1 > .nav-header {
  display: inline;
}

.nav1 > .nav-header > .nav-title {
  display: inline-block;
  font-size: 22px;
  color: #1a1c20;
  padding: 3px 10px 10px 10px;
  text-align: center;
  text-decoration: none;
}

.nav1 > .nav-btn {
  display: none;
}

.nav1 > .nav-links {
  display: inline;
  float: right;
  font-size: 20px;
  padding-right: 18px;
}

.nav1 > .nav-header > .nav-title > a,
h5 {
  display: inline-block;
  font-size: 28px;
  color: #f9f871;
  padding: 0px 10px 10px 1px;
  text-align: center;
  text-decoration: none;
}

.nav1 > .nav-links > a,
h5 {
  display: inline-block;
  padding: 13px 10px 13px 10px;
  text-decoration: none;
  color: #f9f871;
}

.nav1 > .nav-links > a,
h5:hover {
  color: #f9f871;
}

.nav1 > #nav-check {
  display: none;
}

@media (max-width: 600px) {
  .nav1 > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .nav1 > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 10px;
  }
  .nav1 > .nav-btn > label:hover,
  .nav1 #nav-check:checked ~ .nav-btn > label {
    cursor: pointer;
    /* background-color: #fff600; */
  }
  .nav1 > .nav-btn > label > span {
    background-color: #f9f871;
    display: block;
    margin: 5px 0px 5px 0px;
    transition: 0.4s ease-in-out;
    -webkit-transform: none;
            transform: none;
    width: 30px;
    height: 3px;
  }
  .nav1 > .nav-links {
    position: absolute;
    display: block;
    width: 100%;
    background-color: #14406d;
    height: 0px;
    transition: all 0.6s ease-in;
    overflow-y: hidden;
    top: 50px;
    left: 0px;
    z-index: 100;
    padding-left: 5px;
  }

  .nav1 > .nav-links > a,
  h5 {
    display: block;
    width: 100%;
  }

  .nav1 > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
  }

  .nav1 > #nav-check:checked ~ .nav-links {
    height: 161px;
    overflow-y: auto;
  }

  .nav1 > #nav-check:checked ~ .nav-btn span:nth-of-type(1) {
    -webkit-transform: translate(0%, 240%) rotate(-45deg);
            transform: translate(0%, 240%) rotate(-45deg);
    background-color: #f9f871;
  }

  .nav1 > #nav-check:checked ~ .nav-btn span:nth-of-type(2) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background-color: #f9f871;
  }

  .nav1 > #nav-check:checked ~ .nav-btn span:nth-of-type(3) {
    opacity: 0;
  }
}

